import { getCallIndex, joinCall, applyCall, lockCall } from '@/api/course'
import WeChatShare from "@/mixins/WeChatShare"
import config from "@/config/common.config"
import { countTime, storage } from 'ais-common-utils'
import ThirdShare from '@/components/Common/ThirdShare'
export default {
  components: { ThirdShare },
  mixins:[WeChatShare],
  data() {
    return{
      data:{},
      activityId:this.$route.params.id,
      recordId:this.$route.query.recordId || null,
      show:false,
      config,
      currentTime: parseInt((new Date()).getTime()/1000), //获取当前时间
      timer:null, //计时器
      isUpdate:true
    }
  },
  computed:{
    //是非需要新人解锁
    isNewcomer () {
      return this.data && this.data.call && this.data.call.newcomer
    },
    //规则
    modalData () {
      return [
        `1、收集指定好友数为你打call（须为${this.isNewcomer ? '新' : '注册'}用户）即可免费解锁课程优惠券。`,
        '2、每一个用户在同一个课程中，仅能为一位好友打call', 
        '3、活动结束后则不能打call',
        '4、活动未在规定时间内解锁完成，可再次发起打call'
      ]
    },

    //课程名称
    courseName() {
      if(!this.data || !this.data.call) {
        return ''
      }
      return this.data.call.period === 0 ? this.data.call.courseName : `${this.data.call.courseName}(第${this.data.call.period}期)`
    },
    //优惠倒计时
    endTime() {
      return function(tg) {
        if(!this.data || !this.data.record || !this.data.record.end) {
          return null
        }
        const endTime = countTime(this.data.record.end*1000, this.currentTime*1000, tg)
        return this.data.record.end < this.currentTime ? 0 : endTime
      }
    },
    //助力者身份
    isShare() {
      return this.recordId && !this.data.own
    },
    //活动下架
    isDown() {
      return this.data && this.data.call && (this.data.call.status === 0 || (this.data.call.start>this.currentTime) || (this.data.call.end > 0 && this.data.call.end<this.currentTime ))
    },
    //打call成功(status=1,或者邀请人数大于设定的人数，并且在end时间内)
    isSuccess() {
      if(!this.data || !this.data.record) {
        return false
      }
      return this.data.record.status === 1 || (( this.data.record.invite >= this.data.call.numCond ) && this.currentTime < this.data.record.end)
    },
    //活动倒计时结束
    isEnd() {
      if(!this.data || !this.data.record || !this.data.record.end) {
        return false
      }
      return !this.isSuccess && (this.data.record.status === 4 || (this.currentTime > this.data.record.end))
    },
    //分享设置
    shareOption() {
      if(!Object.keys(this.data).length) {
        return ''
      }
      let shareConfig = {}
      //用任务id，就分享邀请链接
      if(this.data.record && this.data.record.id) {
        shareConfig = {
          desc: this.data.call.share,
          link: `${window.location.origin}/courseCall/${this.$route.params.id}?recordId=${this.data.record.id}`
        }
      }
      return {
        title : this.courseName,
        desc: `我已参加这个课程，你也来一起参加吧!`,
        imgUrl: `${config.IMG_URL.default}${this.data.call.poster}`,
        link: `${window.location.origin}/courseDetail/${this.data.call.courseId}`,
        ...shareConfig
      }
    },
  },
  mounted() {
    this.getListHandle()
    this.timer = setInterval(() =>{
      this.getCurrentTime()
    }, 1000)
  },
  beforeDestroy() {
    clearInterval(this.timer)
  },
  methods:{
    //获取当前时间
    getCurrentTime() {
      this.currentTime = parseInt((new Date()).getTime()/1000)
    },
    async getListHandle() {
      const params = this.recordId ? `${this.activityId}/${this.recordId}` : this.activityId
      const { data } = await getCallIndex(params)
      if(data.code === 0) {
        this.data = data.data
      }
    },
    //参加活动
    async joinClick() {
      const { data } = await joinCall(this.data.call.id)
      if(data.code === 0) {
        await this.getListHandle()
        this.isUpdate = false
        this.$nextTick(() =>{
          this.isUpdate = true
        })
      }
    },
    //直接购买跳转支付页面
    buyClick() {
      if(!storage.get('userStatus')) {
        const url = encodeURIComponent(window.location.origin + `/courseOrder/${this.data.call.courseId}`)
        window.location.href = `${config.POST.school_h5}/school/passport/login?mobile=1&state=${url}`
        return
      }
      this.$router.push({
        path:`/courseOrder/${this.data.call.courseId}`
      })
    },
    //生成海报
    posterClick() {
      this.$router.push({
        path:`/coursePoster/${this.data.record.id}?activityId=${this.activityId}`
      })
    },
    //邀请好友
    inviteClick() {
      this.$refs.ThirdShare.open()
    },
    //打call点击
    async callClick() {
      if(!storage.get('userStatus')) {
        const url = encodeURIComponent(window.location.origin + this.$route.fullPath)
        window.location.href = `${config.POST.school_h5}/school/passport/login?mobile=1&state=${url}&event=v_c_${this.activityId}`
        return
      }
      const { data } = await applyCall(this.recordId)
      if (data.code === 0) {
        this.$toast.success('助力成功!')
        this.getListHandle()
      }
    },
    //打call头像显示
    changeImg(index, key) {
      if(this.data.helps && this.data.helps.length && (index<this.data.helps.length)) {
        if(this.data.helps[index] && this.data.helps[index][key]) {
          return key === 'nickname' ? this.data.helps[index][key] : `${this.data.helps[index][key]}`
        } else {
          return key === 'nickname' ? '' : require('@/assets/images/account/avator.png')
        }
      }
      return key === 'nickname' ? '' : require('@/assets/images/courseDetail/peopleIcon.png')
    },
    freeClick() {
      window.location.replace(`/courseCall/${this.activityId}`)
    },
    finishClick() {
      this.$router.push({
        path:`/courseDetail/${this.data.call.courseId}`
      })
    },
    //立即解锁
    async goFinishClick() {
      const { data } = await lockCall(this.data.record.id)
      if(data.code === 0) {
        this.$toast.success('解锁成功!')
        this.finishClick()
      }
    }
  },
  head(){
    return{
      title:'集call免费学 --艾思云课堂'
    }
  }
}
